import React from 'react'
import './Recentwork.css'
import { Link } from 'react-router-dom'
import Content from '../../Work/Content/Content'

const Recentwork = () => {
   return (
      <div className="Recentwork">
         <h2 className="section-heading" data-scroll data-scroll-speed="-.5">
            <span
               className="left"
               data-scroll
               data-scroll-speed="-2"
               data-scroll-direction="horizontal"
            ></span>{' '}
            RECENT WORK{' '}
            <span
               className="right"
               data-scroll
               data-scroll-speed="2"
               data-scroll-direction="horizontal"
            ></span>
         </h2>

         <Content />

         <Link to="/work">
            <button className="button" data-scroll data-scroll-speed="2">
               Show more
            </button>
         </Link>
      </div>
   )
}

export default Recentwork
