import React from 'react'
import './Title.css'

import { Route } from 'react-router-dom'

const Title = () => {
   return (
      <div className="title">
         <div className="header">
            <div id="hero-text">
               <h1 className="titletext">
                  Brand By Agence is a global creative branding studio founded
                  in India. We make brands future-ready.
               </h1>
               <div>
                  <Route
                     render={({ history }) => (
                        <button
                           className="button"
                           id="abc"
                           data-scroll
                           data-scroll-speed="-1"
                           onClick={() => {
                              history.push('/about')
                           }}
                        >
                           Read more
                        </button>
                     )}
                  />
               </div>
            </div>
            <div className="hpscroll">
               <span>scroll</span>
               <div className="scrolll"></div>
            </div>
         </div>
         {/* <img
            className="spaceship"
            data-scroll
            data-scroll-speed="-2"
            src={require('../../../bulb-min.png')}
            alt=""
         />
         <img
            className="homw im"
            data-scroll
            data-scroll-speed="-2"
            src={require('../../../characters-min.png')}
            alt=""
         />
         <img
            className="plants"
            data-scroll
            data-scroll-speed="-2"
            src={require('../../../plants-min.png')}
            alt=""
         /> */}
      </div>
   )
}

export default Title
