import React from 'react'
import './Contact.css'
import emailjs from 'emailjs-com'

const Form = () => {
   function sendEmail(e) {
      e.preventDefault()
      emailjs
         .sendForm(
            'service_alevgyo',
            'template_r4h0rsb',
            e.target,
            'user_Zc5chSbXvhMOPkoe9Ypnq'
         )
         .then(
            (result) => {
               alert('E-mail Sent')
               console.log(result.text)
            },
            (error) => {
               console.log(error.text)
            }
         )
      e.target.reset()
   }
   return (
      <div className="Forma" data-scroll-section>
         <div id="container00">
            <div className="he">
               <h1> Get in Touch </h1>
            </div>

            <form onSubmit={sendEmail} id="contact_form">
               <div className="name">
                  <label className="con" htmlFor="name"></label>
                  <input
                     className="contact"
                     type="texta"
                     placeholder="Your Name"
                     name="name"
                     id="name_input"
                     required
                  />
               </div>
               <div className="email">
                  <label className="con" htmlFor="email"></label>
                  <input
                     className="contact"
                     type="email"
                     placeholder="Your e-mail "
                     name="email"
                     id="email_input"
                     required
                  />
               </div>
               <div className="telephone">
                  <label className="con" htmlFor="name"></label>
                  <input
                     className="contact"
                     type="tel"
                     placeholder="Your number"
                     name="contact_number"
                     id="telephone_input"
                     required
                  />
               </div>
               <div className="message">
                  <label className="con" htmlFor="message"></label>
                  <textarea
                     className="contact"
                     name="message"
                     placeholder="I'd like to chat about"
                     id="message_input"
                     cols="30"
                     rows="5"
                     required
                  ></textarea>
               </div>
               <div className="submit">
                  <button
                     className="button"
                     type="submit"
                     value="Send Message"
                     id="form_button"
                  >
                     Submit
                  </button>
               </div>
            </form>
         </div>
         <img
            className="contimg"
            src={require('../../laptop male (1) (1).png')}
            alt=""
         />
      </div>
   )
}

export default Form
