import React from 'react'
import './App.css'
import LocomotiveScroll from '@deli/locomotive-scroll'

import Cursor from './Component/Cursor/Corsor'
import Navbar from './Component/Navbar/Navbar'
import Footer from './Component/Footer/Footer'
import { Switch, Route, withRouter } from 'react-router-dom'
import Work from './Component/Work/Work'
import Yellowdoor from './Component/Work/Yellowdoor/Yellowdoor'
import Landingpage from './Component/Landingpage/Landingpage'
import About from './Component/About/About'
// import Error from './Component/ErrorPage/Error'
import Contact from './Component/Contact/Contact'
class App extends React.Component {
   componentDidMount() {
      const overlay = document.getElementById('overlay')
      setTimeout(() => {
         const scroll = new LocomotiveScroll({
            el: document.querySelector('[data-scroll-container]'),
            smooth: true,
            smoothMobile: true,
            repeat: true,
         })

         scroll.on('scroll', (position) => {
            if (position.scroll.y > 10) {
               overlay.classList.remove('show')
            }
         })

         this.props.history.listen((location, action) => {
            setTimeout(() => {
               scroll.update()
            }, 100)
         })
      }, 100)
   }

   render() {
      return (
         <div className="App backww" data-scroll-container>
            <Cursor />
            <Navbar />
            {/* <Pages location={this.props.location} /> */}
            <Switch location={this.props.location}>
               <Route path="/" exact component={Landingpage} />
               <Route path="/Agence" exact component={Landingpage} />
               <Route path="/home" exact component={Landingpage} />
               <Route path="/about" exact component={About} />
               <Route path="/contact" exact component={Contact} />
               <Route path="/contact" exact component={Contact} />
               <Route path="/yellowdoor" exact component={Yellowdoor} />
               <Route path="/work" exact component={Work} />
               <Route component={Landingpage} />
            </Switch>
            <Footer />
         </div>
      )
   }
}

export default withRouter(App)
