import React, { useEffect } from 'react'

import './Navbar.css'
import $ from 'jquery'

import { NavLink, Link } from 'react-router-dom'

const Navbar = () => {
   useEffect(() => {
      const overlay = document.getElementById('overlay')

      const closeM = document.getElementById('close')

      const openM = document.getElementById('open')

      const Link = document.getElementById('lin')

      openM.addEventListener('click', function () {
         $(window).scrollTop(0)
         setTimeout(() => {
            overlay.classList.add('show')
         }, 50)
      })
      Link.addEventListener('click', function () {
         overlay.classList.remove('show')
      })

      closeM.addEventListener('click', function () {
         overlay.classList.remove('show')
      })
   })

   return (
      <div className="nav-bar">
         <Link to="/home">
            <img
               data-scroll-section
               className="log"
               src={require('./agence-logo-black.png')}
               alt=""
            />
         </Link>
         <nav id="overlay" className="over-lay" data-scroll-section>
            <div id="stars"></div>
            <div id="stars2"></div>
            <div id="stars3"></div>

            <svg
               className="cross"
               id="close"
               width="33"
               height="33"
               viewBox="0 0 18 17"
               fill="none"
               xmlns="http://www.w3.org/2000/svg"
            >
               <path
                  d="M1 2L17 15M1 15L17 2"
                  stroke="#f5f5f5f5"
                  strokeWidth="3"
               />
            </svg>

            <ul id="lin">
               <li>
                  <NavLink to="/home/">Home</NavLink>
                  {/* <span>Let's get start here</span> */}
               </li>
               <li>
                  <NavLink to="/about">About</NavLink>
                  {/* <span>Find out what you need</span> */}
               </li>
               <li>
                  <NavLink to="/work">Work</NavLink>
                  {/* <span>See what i've done</span> */}
               </li>
               <li>
                  <NavLink to="/contact">Contact</NavLink>
                  {/* <span>Let me introduce myself</span> */}
               </li>
            </ul>
         </nav>

         <div className="container000" data-scroll-section>
            <svg
               className="menu"
               id="open"
               width="33"
               height="33"
               viewBox="0 0 19 16"
               fill="none"
               xmlns="http://www.w3.org/2000/svg"
            >
               <path
                  d="M0 2H18.5M0 8H18.5M0 14.5H18.5"
                  stroke="#000000"
                  strokeWidth="3"
               />
            </svg>
         </div>
      </div>
   )
}

export default Navbar
