import React from 'react'
import { Link } from 'react-router-dom'

const Content = () => {
   return (
      <div className="row">
         <div className="container-work">
            <div
               className="test_box box-01 col-xs-12 col-md-12"
               data-scroll
               data-scroll-speed="2"
            >
               <div className="inner">
                  <Link to="/yellowdoor" className="test_click">
                     <div className="flex_this">
                        <span className="test_title">
                           Yellow Door - Branding and Designing
                        </span>
                     </div>
                  </Link>
               </div>
            </div>
            <div
               className="test_box box-02 col-xs-12 col-md-6"
               data-scroll
               data-scroll-speed="+0.1"
               data-scroll-direction="horizontal"
            >
               <div className="inner">
                  <Link to="/yellowdoor" className="test_click">
                     <div className="flex_this">
                        <span className="test_title">
                           Yellow Door - Branding and Designing
                        </span>
                     </div>
                  </Link>
               </div>
            </div>
            <div
               className="test_box box-03 col-xs-12 col-md-6"
               data-scroll
               data-scroll-speed="-0.1"
               data-scroll-direction="horizontal"
            >
               <div className="inner">
                  <Link to="/yellowdoor" className="test_click">
                     <div className="flex_this">
                        <span className="test_title">
                           Yellow Door - Branding and Designing
                        </span>
                     </div>
                  </Link>
               </div>
            </div>
         </div>
      </div>
   )
}

export default Content
